.App {
  text-align: center;
  background-color: black;
}

.infoText {
  color: white;
  font: bold;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 40px;
}

.infoContainer {
	border: 3px solid white;
	padding: 30px;
	margin: 20px;
	margin-bottom: 150px;
	border-radius: 15px;
}

.infoBullet {
  color: white;
  font-size: 15px;
  text-align: left;
  margin-top: 15px;
  margin-left: 30px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.link {
  color: rgb(150, 0, 0);
  font-weight: bold;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
